}

/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  pointer-events: none;
}
@media only screen and (max-width: 991px) {
  .swiper-nav {
    justify-content: center;
    gap: 20px;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 37px;
  background-color: #000000;
  border-radius: 10px 6px 10px 6px;
  transform: skewX(-8deg);
  cursor: pointer;
  pointer-events: auto;
  transition: background-color 0.3s ease;
}
.swiper-nav .swiper-btn-prev:hover,
.swiper-nav .swiper-btn-next:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transform: skewX(8deg);
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 16px !important;
  z-index: 12;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  background-color: #ffffff;
  margin: 0 !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  opacity: 1;
}

/* Blocks */
.container {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 575px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  max-width: 300px;
  width: 100%;
  padding: 21px 20px;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #0264fa;
  color: #ffffff;
  font-size: 17px;
  line-height: 1.3;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 991px) {
  .button {
    font-size: calc(16px + (17 - 16) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .button {
    font-size: calc(14px + (16 - 14) * (100vw - 0px) / (768 - 0));
  }
}
.button:hover {
  background-color: rgba(2, 100, 250, 0.7);
}

.button-up {
  position: fixed;
  right: 50px;
  bottom: 35px;
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  padding: 5px;
  border-radius: 3px;
  background-color: #0264fa;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
  cursor: pointer;
}
@media only screen and (max-width: 991px) {
  .button-up {
    right: 15px;
    bottom: 15px;
  }
}
.button-up:hover {
  background-color: rgba(2, 100, 250, 0.7);
}
.button-up--visible {
  opacity: 1;
  pointer-events: auto;
}

.title {
